<h1 id="web-sessions" tabindex="-1"><a class="header-anchor" href="#web-sessions" aria-hidden="true">#</a> Web Sessions</h1>
<p>A session is something a web application can use to remember information about a user, similiar to cookies, but unlike cookies, sessions are stored on the server.</p>
<h2 id="what-is-a-session" tabindex="-1"><a class="header-anchor" href="#what-is-a-session" aria-hidden="true">#</a> What is a session?</h2>
<p>A session is a collection of key-value pairs stored on the server that belongs to a specific client (each client has its own session on the server). A server can use a session to remember information about a client. A session is quite similar to a collection of cookies, but clients can't read or write the key-value pairs in the session; only the server can read and write the key-value pairs in the session. So if the server puts a value in a client's session, it can be sure that that value is valid (it can't be changed by anyone except the server itself).</p>
<FigureNumber /> below shows an example of how a session can be used to remember a client's name.
<Figure caption="Example of Server using session to remember information from a client.">
<RenderMermaid graph-definition="
sequenceDiagram
		participant Client as Client A
		participant Server
		participant Session as Client A Session
		Client->>+Server: 1. HTTP Request: My name's Alice
		Server->>+Session: 2. Store name=Alice
		Note right of Session: name=Alice
		Session->>-Server: 3. Stored!
		Server-->>-Client: 4. HTTP Response: Hi Alice!
		Client->>+Server: 5. HTTP Request: What is my name?
		Server->>+Session: 6. Get name
		Session->>-Server: 7. Alice
		Server-->>-Client: 6. HTTP Response: Your name is Alice!
" />
</Figure>
<h2 id="how-long-does-a-session-exist" tabindex="-1"><a class="header-anchor" href="#how-long-does-a-session-exist" aria-hidden="true">#</a> How long does a session exist?</h2>
<p>The session only exists &quot;as long as the user use the website&quot;. In many web frameworks, a session only exists for 20 minutes (can usually be configured), but each time the user visits a new page on the website, those 20 minutes are &quot;refreshed&quot;, and the session will continue to live for 20 minutes again. That means if you start to use a website, you get a new session the server can use to remember information about you, and if you don't send a new HTTP request within 20 minutes, that session will be deleted, and the server will forget all the information it had stored about you in your session. With the next HTTP request you send to the server after 20 minutes, you will get a new session.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>You might have noticed that if you login on a website, start reading a long page for ~30 minutes, and then go to another page on the website, that you are not logged in anymore? That's because the session has been deleted since you haven't been active on (sent HTTP request to) that website for a while. It's not a bug; it's supposed to work like this.</p>
</div>
<h2 id="what-are-sessions-used-for" tabindex="-1"><a class="header-anchor" href="#what-are-sessions-used-for" aria-hidden="true">#</a> What are sessions used for?</h2>
<p>So, when you need to remember information about a client, you can use either cookies or sessions. When should you use which one? A rule of thumb is that you should use sessions if you need to remember temporary information about a client the client should not have access to. Otherwise, a cookie might be the better choice.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>If you need to remember which account a client has logged in to, then you should remember this in the client's session, because you don't want the client to be able to change this information.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>If you want to remember which words the user has searched for in the search form on your website, then you might be better of storing that information in a cookie. The user only harms herself by modifying the cookie, and by using a cookie, we can remember that information for a much longer time compared to using a session.</p>
<p>One downside with using a cookie in this example is that the cookie will be passed to the server with every single request the client makes. For cookies containing little data, this is probably not a problem, but if the cookie contains a lot of data, this might slow down the performance, and it will increase the user's data usage, which is bad if the user has a limited data plan.</p>
</div>
<h2 id="how-are-sessions-implemented" tabindex="-1"><a class="header-anchor" href="#how-are-sessions-implemented" aria-hidden="true">#</a> How are sessions implemented?</h2>
<p>Session might sound complicated, but they are most often very easy to use, because the framework/libraries we use have implemented a nice interface for us through which we can use sessions without having to bother about the details ourselves.</p>
<p>How the server store the session on the server is up to the server to decide. It could store the sessions in temporary files, or it could store the sessions in a database. Web programmers usually don't need to worry about this, because the framework/libraries we use takes care of this for us.</p>
<p>The trickiest part with sessions is for the server to figure out a way to remember which session that belongs to which client. Lucky for us, experienced developers have already found a good way doing this using cookies, but for us to understand why that way is as complicated as it is, let's try to find a way on our own first!</p>
<h3 id="identifying-clients" tabindex="-1"><a class="header-anchor" href="#identifying-clients" aria-hidden="true">#</a> Identifying clients</h3>
<p>So, first we need to be able to uniquely identify clients. We need a unique identifier for each client to be able to map the sessions stored on the server to individual clients.</p>
<p>When using HTTP, it is very tempting to use the client's IP address as the unique identifier for the client, because the Internet is built on that each computer connected to the Internet should have its own unique IP address! But there is a number of problems with this approach.</p>
<h4 id="ip-problem-1-ip-addresses-are-re-used" tabindex="-1"><a class="header-anchor" href="#ip-problem-1-ip-addresses-are-re-used" aria-hidden="true">#</a> IP problem 1: IP addresses are re-used</h4>
<p>IP addresses are re-used. So if Alice connects her computer to the Internet and get the IP address <code>1.2.3.4</code>, and then login on the website <code>bank.com</code>, she would get a session there that remembers that she has logged in to the <code>Alice</code> account, and that session is connected to her IP address <code>1.2.3.4</code>. If she then disconnects from the Internet, someone else will receive that IP address. Let's say Bob starts and connect his computer to the Internet, and now Bob gets the IP address <code>1.2.3.4</code>. Then if Bob sends HTTP requests to <code>bank.com</code>, he would take over the session that was created for Alice, and he would now be logged in to her account! This is visualized in <FigureNumber /> below.</p>
<Figure caption="Example of why using IP addresses to identify clients are bad.">
<RenderMermaid graph-definition="
sequenceDiagram
		participant Bob
		participant Alice
		participant Server
		participant Session as Alice Session
		Alice->>Alice: Connect computer to Internet, get IP 1.2.3.4
		Alice->>+Server: Login, ip=1.2.3.4, username=Alice, password=abc123
		Server->>Server: checkCredentials(Alice, abc123)
		Server->>+Session: Store ip=1.2.3.4, loggedInAccount=Alice
		Note right of Session: ip=1.2.3.4, loggedInAccount=Alice
		Session->>-Server: Stored!
		Server-->>-Alice: Login successful!
		Alice->>Alice: Disconnect computer from Internet
		Bob->>Bob: Connect computer to Internet, get IP 1.2.3.4
		Bob->>+Server: Transfer money, ip=1.2.3.4, to=Bob, amount=100
		Server->>+Session: Get loggedInAccount ip=1.2.3.4
		Session->>-Server: Alice
		Server->>Server: transfer(from=Alice, to=Bob, amount=100)
		Server-->>-Bob: Transfer successful!
" />
</Figure>
<p>This way, Bob can try to repeatedly connect and disconnect his computer to the Internet to get a new IP address all the time, and each time he also sends a request to the server about transferring money from the logged in account to his own account. Not often, but sometimes he will take over a session from someone who just used the server and still have an active session there!</p>
<p>So, IP addresses are not good unique identifiers for clients, because they will change over time.</p>
<h4 id="ip-problem-2-ip-addresses-can-be-guessed" tabindex="-1"><a class="header-anchor" href="#ip-problem-2-ip-addresses-can-be-guessed" aria-hidden="true">#</a> IP Problem 2: IP addresses can be guessed</h4>
<p>The problem described above is actually not only a problem when a client disconnects from the Internet. The way the Internet Protocol works (which is also true for the HTTP protocol, since it's built on the Internet Protocol) is that each computer should write its own IP address in all messages it sends to another computer. However, there's no way to check if a client actually has put its own IP address in the message, so nothing stops a hacker from using another IP address</p>
<p>When a client uses another IP address as its own, the client won't be able to receive the HTTP response from the server, but that is not needed if the attack is about getting the server to carry out a side effect of some kind (like transferring money from the logged in account to another account). This is visualized in <FigureNumber /> below.</p>
<Figure caption="Example of why using IP addresses to identify clients are bad.">
<RenderMermaid graph-definition="
sequenceDiagram
		participant Bob
		participant Alice
		participant Server
		participant Session as Alice Session
		Alice->>Alice: Connect computer to Internet, get IP 1.2.3.4
		Alice->>+Server: Login, ip=1.2.3.4, username=Alice, password=abc123
		Server->>Server: checkCredentials(Alice, abc123)
		Server->>+Session: Store ip=1.2.3.4, loggedInAccount=Alice
		Note right of Session: ip=1.2.3.4, loggedInAccount=Alice
		Session->>-Server: Stored!
		Server-->>-Alice: Login successful!
		Bob->>Bob: Connect computer to Internet, get IP 9.8.7.6
		Bob->>+Server: Transfer money, ip=1.2.3.4, to=Bob, amount=100
		Server->>+Session: Get loggedInAccount ip=1.2.3.4
		Session->>-Server: Alice
		Server->>Server: transfer(from=Alice, to=Bob, amount=100)
		Server-->>-Bob: Transfer successful!
" />
</Figure>
<p>This way, Bob can try to repeatedly generate new IP addresses and send requests to transfer money from the logged in account to his own account. Not often, but sometimes he will use an IP address with an active session on the server, and the transfer will succeed.</p>
<h4 id="the-solution" tabindex="-1"><a class="header-anchor" href="#the-solution" aria-hidden="true">#</a> The solution</h4>
<p>We can't use IP addresses to uniquely identify clients. Instead, we will generate a unique identifier for each client. Now, it's important that we don't generate the identifiers starting with 1, and then increment by 1, so we would get 1, 2, 3, and so one, or similar. The reason is the same as the one described in <em>IP Problem 2</em>; hackers can easily guess these identifiers and send their own requests to the server using them.</p>
<p>So, the identifiers needs to be hard to guess for hackers. Therefor one typically use long strings consisting of random characters as the identifier, so one client could get the identifier <code>204dc1145c4545f094208a077f8fa9d4</code>, and another client could get the identifier <code>12fa3f48e3314cf2869884345e8543ef</code>. It is extremely unlikely a hacker would manage to generate his own identifier that would identify an active session this way.</p>
<p>Further more, these identifiers does now not really identify individual clients, but we will instead use them to identify individual sessions. Therefor these identifiers are called <em>session ids</em>.  This is visualized in <FigureNumber /> below.</p>
<Figure caption="Example of why using IP addresses to identify clients are bad.">
<RenderMermaid graph-definition="
sequenceDiagram
		participant Alice
		participant Server
		participant Session as Alice Session
		Alice->>+Server: Login, username=Alice, password=abc123
		Server->>Server: checkCredentials(Alice, abc123)
		Server->>+Session: Store loggedInAccount=Alice
		Note right of Session: id=204dc1..., loggedInAccount=Alice
		Session->>-Server: Stored, id=204dc1...!
		Server-->>-Alice: Login successful, sessionId=204dc1...
		Note left of Alice: sessionId=204dc1...
		Alice->>+Server: Transfer money, sessionId=204dc1..., to=Claire, amount=100
		Server->>+Session: Get loggedInAccount id=204dc1...
		Session->>-Server: Alice
		Server->>Server: transfer(from=Alice, to=Claire, amount=100)
		Server-->>-Alice: Transfer successful!
" />
</Figure>
<h2 id="when-are-sessions-created" tabindex="-1"><a class="header-anchor" href="#when-are-sessions-created" aria-hidden="true">#</a> When are sessions created?</h2>
<p>There are different strategies the server can use to decide when is should create the session for a new client. One strategy is to create the session as soon as it receives a request from a new client. This way each client is guaranteed to always have its own session. But this is a bit wasteful when the server has clients it doesn't need to remember anything about.</p>
<p>Another strategy is to create a new session for the client the first time the server needs to remember something about that client. This way, no empty sessions will exist. Furthermore,  😃</p>
<p>Which strategy you want to use can usually be configured in the framework/library you use to work with the sessions.</p>
<h2 id="anything-special-to-think-about-when-using-sessions" tabindex="-1"><a class="header-anchor" href="#anything-special-to-think-about-when-using-sessions" aria-hidden="true">#</a> Anything special to think about when using sessions?</h2>
<h3 id="sdfsdfsd" tabindex="-1"><a class="header-anchor" href="#sdfsdfsd" aria-hidden="true">#</a> sdfsdfsd</h3>
<p>Since sessions usually use cookies to remember which session that belongs to which client, you need to consider the cookie law and the GDPR.</p>
<p>...</p>
<h3 id="securing-the-session-id" tabindex="-1"><a class="header-anchor" href="#securing-the-session-id" aria-hidden="true">#</a> Securing the session id</h3>
<p>The session id is really important. Use HTTPS.</p>
<h3 id="clients-can-create-new-sessions" tabindex="-1"><a class="header-anchor" href="#clients-can-create-new-sessions" aria-hidden="true">#</a> Clients can create new sessions</h3>
<p>Clients can easilly create new sessions. By sending an HTTP request to the server that doesn't contain a session id, the server will create a new session for the client. Because of this, you often can't use the session to keep track of things you want to forbid the client to do.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>If you use a counter to keep track of how many times a user has failed to login to an account, and prevent the client from login in to an account when the counter reaches a specific number (e.g. 5 failed login attempts), then you can't store the counter in the session, because the user can easilly send a new HTTP request to the server without a session id, and then get a new session where the counter is 0 again.</p>
</div>
<h2 id="using-sessions-in-practise" tabindex="-1"><a class="header-anchor" href="#using-sessions-in-practise" aria-hidden="true">#</a> Using sessions in practise</h2>
<CodeGroup>
<CodeGroupItem title="Express">
<p>Express does not have built-in support for sessions, but it can easily be added through a middleware function from the <a href="https://github.com/expressjs/session" target="_blank" rel="noopener noreferrer">express-session<OutboundLink/></a> package.</p>
<ol>
<li>Install the <code>express-session</code> package by running the command <code>npm install express-session</code> in your project folder.</li>
<li>Add middleware function with session support from the <code>express-session</code> package in your code, e.g.:</li>
</ol>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> express <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'express'</span><span class="token punctuation">)</span>
<span class="token keyword">const</span> expressSession <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'express-session'</span><span class="token punctuation">)</span>

<span class="token keyword">const</span> app <span class="token operator">=</span> <span class="token function">express</span><span class="token punctuation">(</span><span class="token punctuation">)</span>

<span class="token comment">// Add the middleware function from express-session.</span>
app<span class="token punctuation">.</span><span class="token function">use</span><span class="token punctuation">(</span><span class="token function">expressSession</span><span class="token punctuation">(</span><span class="token punctuation">{</span>
	<span class="token comment">// Let's cover these options later...</span>
	secret<span class="token operator">:</span> <span class="token string">'some characters no one can guess...'</span><span class="token punctuation">,</span>
	resave<span class="token operator">:</span> <span class="token boolean">false</span><span class="token punctuation">,</span>
	saveUninitialized<span class="token operator">:</span> <span class="token boolean">true</span><span class="token punctuation">,</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">)</span>

<span class="token comment">// Use app as before...</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div><p>When we call <code>expressSession</code> we get back a middleware function that we add to our express application object. This middleware will add the <code>session</code> property to the <code>request</code> objects we have. If we want to store a value in the client's session, then we simply add that value to this object, e.g. <code>request.session.theKey = &quot;The value&quot;</code>. If we want to read out a value we have previosuly stored in the session, then we simply read it out from this object, e.g. <code>const theValue = request.session.theKey</code>.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Below is an example of code that in the session stores number of times the user has visisted a specific page.</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>app<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string">"/count-visits"</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">request<span class="token punctuation">,</span> response</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
	
	<span class="token comment">// If we haven't put the counter in the session before,</span>
	<span class="token comment">// that means the user is visiting this page for the</span>
	<span class="token comment">// first time, and we need to create the counter.</span>
	<span class="token keyword">if</span><span class="token punctuation">(</span><span class="token operator">!</span>request<span class="token punctuation">.</span>session<span class="token punctuation">.</span>counter<span class="token punctuation">)</span><span class="token punctuation">{</span>
		request<span class="token punctuation">.</span>session<span class="token punctuation">.</span>counter <span class="token operator">=</span> <span class="token number">0</span>
	<span class="token punctuation">}</span>
	
	<span class="token comment">// Increment the counter by one.</span>
	request<span class="token punctuation">.</span>session<span class="token punctuation">.</span>counter <span class="token operator">+=</span> <span class="token number">1</span>
	
	response<span class="token punctuation">.</span><span class="token function">send</span><span class="token punctuation">(</span><span class="token string">"You have visited this page "</span><span class="token operator">+</span>request<span class="token punctuation">.</span>session<span class="token punctuation">.</span>counter<span class="token operator">+</span><span class="token string">" times."</span><span class="token punctuation">)</span>
	
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br></div></div><p>The first time a user sends a GET request to <code>/count-visits</code>, she will get back the text <em>You have visited this page 1 times.</em>. The second time she sends the same request, she will get back the text <em>You have visited this page 2 times.</em>, and so on. Notice that each user will have their own counter (because each user will get its own session).</p>
</div>
</CodeGroupItem>
<p>When you create the session middleware function, you can configure it by passing it an object with options. There are three mandatory options.</p>
<p>The <code>secret</code> option is a value used to generate the session ids. It doesn't matter what you use here as long as no one can figure it out. If a hacker knows which secret you are using, she might be able to figure out which session ids your users will get, and can then easilly hijack (take over) their sessions (send HTTP request with the same session id, so the server think the requests are sent from the user the session belongs to), which is a major security vulnerability.</p>
<p>The <code>resave</code> option...</p>
<p>The <code>saveUninitialized</code> option...</p>
<p>:::</p>
<CodeGroupItem title="PHP">
<p>PHP has built-in support for sessions, but to use them you first need to &quot;activate them&quot; by calling the function <code>session_start()</code>. You need to call this function each time you receive an HTTP request, and it will create a new session for the client if the client doesn't have one, or re-use the existing one the client already have.</p>
<p>When you have called <code>session_start()</code> you can use the special variable <code>$_SESSION</code> to write values to the session and to retrieve values from the session. For example, to add a new value to the session you would use  <code>$_SESSION[&quot;theKey&quot;] = &quot;The value&quot;;</code>, and to obtain a value from the session you would use <code>$theValue = $_SESSSION[&quot;theKey&quot;];</code>.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Here is an example of a PHP page that counts how many times the user have fetched it.</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token php language-php"><span class="token delimiter important">&lt;?php</span>

<span class="token function">session_start</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// If no counter exists in the session, create it.</span>
<span class="token keyword">if</span><span class="token punctuation">(</span><span class="token operator">!</span><span class="token keyword">isset</span><span class="token punctuation">(</span><span class="token variable">$_SESSION</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'counter'</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
	<span class="token variable">$_SESSION</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'counter'</span><span class="token punctuation">]</span> <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span>

<span class="token comment">// Increment the counter in the session.</span>
<span class="token variable">$_SESSION</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'counter'</span><span class="token punctuation">]</span> <span class="token operator">+=</span> <span class="token number">1</span><span class="token punctuation">;</span>

<span class="token keyword">echo</span> <span class="token string double-quoted-string">"You have visisted this page "</span><span class="token operator">.</span><span class="token variable">$_SESSION</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'counter'</span><span class="token punctuation">]</span><span class="token operator">.</span><span class="token string double-quoted-string">" times."</span><span class="token punctuation">;</span>

<span class="token delimiter important">?></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br></div></div><p>The first time a user visits the page, she will get back the text <em>You have visited this page 1 times.</em>. The second time she sends the same request, she will get back the text <em>You have visited this page 2 times.</em>, and so on. Notice that each user will have their own counter (because each user will get its own session).</p>
</div>
</CodeGroupItem>
<p>:::</p>
</CodeGroup>
<h2 id="a-note-on-stateless" tabindex="-1"><a class="header-anchor" href="#a-note-on-stateless" aria-hidden="true">#</a> A Note on Stateless</h2>
<p>Depending on where you store your sessions, your web application either become statefull or remain stateless, and this affects how hard it will be to scale your web application in the future. We will not get into the details of scaling here, but be aware of that if the sessions are stored on the same server that is running your web application (for example in files, which is the case when using the default configuration of PHP), your application becomes statefull and a bit harder to scale. If you instead store the sessions in a database running on a separate sever, your web application remain stateless and is a bit easier to scale.</p>
